import styles from './index.module.css'

const Heading = ({ children, color = 'black', level = 1, size = 'XXL', ...props }) => {
  const Tag = `h${level}`
  return <Tag className={`heading ${styles[`heading--${size.toLowerCase()}`]} ${styles[`heading--${color}`]}`} {...props}>{children}</Tag>
}

const HeadingXXL = ({ children }) => (
  <Heading>{children}</Heading>
)

const HeadingXL = ({ color = 'black', children }) => (
  <Heading color={color} level={2} size='XL' style={{ borderBottom: `1px solid ${color}` }}>
    {children}
  </Heading>
)

const Introduction = () => (
  <Container>
    <div className={styles.introduction}>
      <img src={me} style={{ boxShadow: '0 2px 26px 0 rgba(0, 0, 0, 0.5)', width: '260px', height: '260px' }} alt='me' />
      <div className={styles.introduction__contentWrap}>
        <HeadingXXL>Freelance JavaScript Developer</HeadingXXL>
        <p>
          Im a passionate fullstack web / mobile developer. My main focus is JavaScript since it allows writing your entire web and (native) mobile stack using the same programming language in a very effective way.
      </p>
        <p>
          Im very comfortable with setting up new projects and taking a client facing technical lead role even if the stack includes a language or technology that is new or unfamiliar to me.
      </p>
      </div>
    </div>
  </Container>
)

